.outer {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: black;
  align-items: center;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 3px solid #ffbb00;
  padding: 10px;
  border-radius: 10px;
}

form input {
  width: 350px;
  padding: 10px;
  font-size: 17px;
  border-radius: 10px;
  margin: 10px;
  color: black;
  border: none;
}
form select {
  width: 350px;
  padding: 10px;
  font-size: 17px;
  border-radius: 10px;
  margin: 10px;
  color: black;
  border: none;
  border-bottom: 2px solid #ffbb00;
}
div button {
  background-color: #ff9900;
  padding: 5px;
}
label {
  font-size: 20px;
  color: White;
}
