.table-outer {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: black;
  align-items: center;
}

table {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 3px solid #ffbb00;
  padding: 10px;
  border-radius: 10px;
}
table tr td {
  text-align: center;
  width: 350px;
  padding: 10px;
  font-size: 17px;
  border-radius: 10px;
  margin: 10px;
  color: white;
  border: none;
  border-bottom: 2px solid #ffbb00;
}
th {
  width: 350px;
  padding: 10px;
  font-size: 17px;
  border-radius: 10px;
  margin: 10px;
  font-size: 20px;
  color: white;
  border: none;
  background-color: #ffbb00;
}

table button {
  background-color: #ff9900;
  padding: 5px;
}
